/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'Suisse Works';
  font-style: normal;
  font-weight: 400;
  src: url('https://jjl-dev.github.io/jjl/static/media/SuisseWorks-Regular-WebS.1fc9b58b.woff2'); /* IE9 Compat Modes */
  src: local('Suisse Works'), local('Suisse Works'),
       url('https://jjl-dev.github.io/jjl/static/media/SuisseWorks-Regular-WebS.1fc9b58b.woff2?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('https://jjl-dev.github.io/jjl/static/media/SuisseWorks-Regular-WebS.1fc9b58b.woff2') format('woff2'), /* Super Modern Browsers */
       url('https://jjl-dev.github.io/jjl/static/media/SuisseWorks-Regular-WebS.1fc9b58b.woff2') format('woff'), /* Modern Browsers */
}

@font-face {
  font-family: 'Suisse Works Intl';
  font-style: normal;
  font-weight: 400;
  src: url('https://jjl-dev.github.io/jjl/static/media/SuisseIntl-Book-WebM.76e05da9.woff2'); /* IE9 Compat Modes */
  src: local('Suisse Works'), local('Suisse Works'),
       url('https://jjl-dev.github.io/jjl/static/media/SuisseIntl-Book-WebM.76e05da9.woff2?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('https://jjl-dev.github.io/jjl/static/media/SuisseIntl-Book-WebM.76e05da9.woff2') format('woff2'), /* Super Modern Browsers */
       url('https://jjl-dev.github.io/jjl/static/media/SuisseIntl-Book-WebM.76e05da9.woff2') format('woff'), /* Modern Browsers */
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
  line-height: 1;
  font-family: Suisse Works,	-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
-webkit-font-smoothing: antialiased;
-moz-osx-font-smoothing: grayscale;
}
a{
	text-decoration: none;
	color: #000;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

hr { 
	display: block; 
	height: 1px;
	border: 0; 
	border-top: 1px solid #E5E5E5; 
  padding: 0; 
  margin: 0;
}

img{
  display: block;
}

.is-active{
  font-style: italic;

}